<template>
  <div class="personal-content-body">
    <div class="content">
      <div class="content-title clear">
        <div class="left">抄送我的</div>
        <el-input placeholder="搜索" v-model="keyWord" class="header-right-time right" :clearable='true' @change="getListData(1)">
          <i slot="prefix" class="el-input__icon el-icon-search "></i>
        </el-input>
      </div>
      <div class="historyapply-group">
        <div class="clear table-group-title">
          <div class="breadcrumb left">
            <el-breadcrumb separator-class="el-icon-arrow-right">
              <el-breadcrumb-item>抄送我的（{{total}}）</el-breadcrumb-item>
            </el-breadcrumb>
          </div>
          <div class="right">
            <router-link :to="{name:'createApply'}">
              <div class="table-group-title-btn btn-blue">
                <img src="../../images/createapply_icon_create.png">新建审批
              </div>
            </router-link>
          </div>
          <div class="line"></div>
        </div>
        <div class="historyapply-group-tablebox">
          <div class="table-normal-box">
            <table class="table-normal">
              <tr>
                <th>序号</th>
                <th>审批标题</th>
                <th>审批类型</th>
                <th width="100">审批申请人</th>
                <th width="120">审批申请时间</th>
                <th width="90">审批状态</th>
                <th width="120">操作</th>
              </tr>
              <tr v-for="(item,index) in list" :key="item.seq">
                <td>{{pageSize*(currentPage-1)+index+1}}</td>
                <td>{{item.title}}</td>
                <td>{{item.kind}}</td>
                <td>{{item.name}}</td>
                <td>{{item.add_time}}</td>
                <td>{{item.status_str}}</td>
                <td>
                  <span class="table-link" @click="gotoViewapply(item.approval_id,item.status)">查看</span>
                </td>
              </tr>
            </table>
            <div class="table-nodata" v-if="total==0">
              <img src="../../images/table_nodata.png"> 没有对应数据
            </div>
          </div>
          <div class="pagination-normal" v-if="total!=0">
            <el-pagination background layout="prev, pager, next" :total="total" :page-size="pageSize" prev-text="上一页" next-text="下一页" @current-change="currentChange" :current-page="currentPage">
            </el-pagination>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import {
  showCopy
} from '../../config/api'
import {
  dialogConfirmHasbtn,
  dialogConfirm
} from '../../config/mUtils'
export default {
  data() {
    return {
      list: [],
      pageSize: 10,
      keyWord: '',
      total: 0,
      applytype: 4,
      applytypelist: [{
        id: 1,
        name: '待我审批'
      }, {
        id: 2,
        name: '我已审批'
      }, {
        id: 3,
        name: '我发起的审批'
      }],
      currentPage: 1,
    }
  },
  created() {
    this.getListData(1);
  },
  methods: {
    currentChange(val) {
      if (this.total > this.pageSize) {
        this.getListData(val)
      }
    },
    //获取数据
    getListData(val) {
      this.list = [];
      this.currentPage = val;
      let params = {
        page: this.currentPage,
        pageSize: this.pageSize,
        //status:this.applytype,
        fuzzyQuery: this.keyWord,
        //type:2
      }
      showCopy(params).then(res => {
        let data = res.data.payload;
        this.total = data.total;
        if (data.data.length != 0) {
          data.data.forEach(item => {
            if (item.status == 0) {
              item.status_str = '待审批'
            } else if (item.status == 1) {
              item.status_str = '审批中'
            } else if (item.status == 2) {
              item.status_str = '审批通过'
            } else if (item.status == 3) {
              item.status_str = '待用印'
            } else if (item.status == 4) {
              item.status_str = '完成用印'
            } else if (item.status == 5) {
              item.status_str = '撤回审批'
            } else if (item.status == -1) {
              item.status_str = '审批不通过'
            }
          })
          this.list = data.data;
        }
      })
    },
    changeApplyType() {
      this.getListData(1);
    },
    //前往查看页面
    gotoViewapply(id, status) {
      if (this.applytype == 1 || this.applytype == 2 || this.applytype == 4) {
        status = 2
      } else {
        status = 1
      }
      this.$router.push({
        query: {
          approval_id: id,
          status: status
        },
        name: 'viewApply'
      })
    }
  }
}

</script>
<style lang="scss" scoped>
@import '../../style/modal.scss';
.content {
  width: 100%;
  min-height: 100%;
  background-color: white;
  padding: 36px 40px;
}

.content-title {
  width: 100%;
}

.histroyapply-icon {
  width: 15px;
  height: 15px;
  background: url('../../images/histroyapply_icon.png') no-repeat;
  display: inline-block;
  float: left;
  margin: 2px 6px 0px 0px;
}

.historyapply-group {
  width: 100%;
  border: 1px solid #E4E4E4;
  padding: 18px 0 16px;
}

.historyapply-group-total {
  font-size: 18px;
  color: #3C98E8;
  font-weight: bold;
  line-height: 1em;
  margin-bottom: 35px;
}

.historyapply-group-tablebox {
  // padding: 0px 18px 0px 14px;
  padding: 0px 50px;
  min-height: 440px;
}

.table-normal-box {
  min-height: 440px;
}

.pagination-normal {
  text-align: center;
  margin-top: 20px;
}

.header-right-time {
  margin-right: 10px;
  position: relative;
}

.header-right-time:nth-child(2) {
  margin-right: 0px;
}

.creditCz-box span:hover {
  cursor: pointer;
  color: #3C98E8;
}

.creditCz-box span:nth-child(2) {
  //padding: 0 10px;
}

.btn-cancle {
  border: 1px solid #3C98E8;
  color: #3C98E8;
  margin-right: 20px;
}

.form-group-inputbox {
  width: 470px;
  margin-right: 0px;
}

.breadcrumb {
  padding: 0px 14px;
}

.table-group-title-btn {
  display: flex;
  align-items: center;
  img {
    margin-right: 8px;
  }
}

</style>



